* {
    /* change the default since I haven't gotten to accounting borders yet*/
    box-sizing: border-box;
}

main {
    border: 3px solid #6495ED;
    display: inline-flex;
    flex-direction: column;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    border-radius: 12px;
    padding: 15px 15px;

    flex-grow: 1;

    text-align: center;

    > div {
        display: flex;
        flex-direction: row;
    }
}

.boxes {
    float: left;
    display: flex;
    flex-direction: column;
}

.versions {
    flex-grow: 3;
}

.boxes > h2 {
    text-align: center;
}

textarea {
    flex-grow: 1;
    min-width: 100%;
}

#row2 {
    justify-content: space-evenly;
}

#panel {
    font-weight: bold;
    /* undo other */
    flex-grow: 0;
    align-self: revert;

    > .panel-content {
        /* prevents expanding */
        display: flex;
        flex-direction: column;

        > * {
            /* sub panels */
            display: flex;
            flex-flow: row wrap;
            justify-content: space-around;
        }
    }
}

.panel {
    flex-basis: max-content;
    align-self: stretch;

    &[name='boxControls'], &[name='colors'] {
        flex-direction: column;
        justify-content: space-evenly;
        align-content: center;
    }

    &[name='colors'] {
        align-content: stretch;
        align-items: stretch;
        justify-content: space-evenly;
    }

    &, #colors {
        display: inline-flex;
        flex-wrap: wrap;
        flex-grow: 1;
    }

    justify-content: center;

    #colors {
        color: unset;
        justify-content: space-evenly;

        > .inputLabel {
            display: flex;
            justify-content: space-evenly;

            > span {
                flex-grow: 1;
            }

            > input {
                align-self: flex-end;
            }
        }
    }

    &[name='export'] {
        flex-direction: column;
        justify-content: space-evenly;
        flex-grow: 0;

        .panel[name='format'] {
            max-width: max-content;
            align-self: center;
            flex-grow: 0;
        }

        textarea {
            flex-grow: 1;
            margin-block: 5px;
            min-width: 100%;
            resize: both;
        }
    }
}

.inputLabel {
    display: inline-flex;
    align-items: center;
}

#reset-color {
    margin-top: 1px
}

#panel, button {
    align-self: center;
    text-align: center;
}

.App {
    background-color: #282c34;
    min-height: 100vh;
    /*font-size: calc(10px + 2vmin);*/
    color: white;

    display: flex;
    flex-direction: column;
}

main {
    align-self: stretch;
}

.App-link {
    color: #61dafb;
}
