.lockSize {
    position: relative;
    width: 100%;
}

.lockSize > * {
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
}

.diff {
    overflow: auto;
    text-align: left;
    word-wrap: break-word;
    white-space: pre-wrap;
    display: block;
    cursor: text;
}

.versions > * {
    max-width: 50%;
}

.diff-container {
    flex: 1 2;
}

.version {
    flex-grow: 1;
    flex-shrink: 0;
}

.output {
    display: flex;
    flex-direction: column;
    resize: block;
}

.output {
    flex-grow: 3;
    flex-shrink: 3;
    margin-left: 0.5em;
}

.version > textarea {
    max-width: fit-content;
    resize: inline;
}